import React from "react";
// import 'bootstrap/dist/css/bootstrap.min.css'
import {Button, Form, FormGroup, Label, Input, Row, Col} from "reactstrap";


class FormAddingUser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: props.username,
            email: props.email,
            created: props.created,
            group: props.group,
        }
    }

    handleAddNewUser = () => {
        console.log(this.state)
    }

    render() {
        const {email, username} = this.state;
        return (
            <Form>
                <Row form>
                    <Col md={6}>
                        <FormGroup>
                            <Label for="exampleEmail">Username</Label>
                            <Input value={email} onChange={(e) => this.setState({email: e.target.value})} type="email"
                                   name="email" id="exampleEmail" placeholder="Username"/>
                        </FormGroup>
                    </Col>
                    <Col md={6}>
                        <FormGroup>
                            <Label for="examplePassword">Email</Label>
                            <Input value={username} onChange={(e) => this.setState({username: e.target.value})}
                                   type="text"
                                   name="password" id="examplePassword" placeholder="Email"/>
                        </FormGroup>
                    </Col>
                </Row>
                <FormGroup>
                    <Label for="exampleAddress">Created</Label>
                    <Input type="text" name="address" id="exampleAddress" placeholder="Date"/>
                </FormGroup>
                <FormGroup>
                    <Label for="exampleAddress2">Group</Label>
                    <Input type="text" name="address2" id="exampleAddress2" placeholder="Choose the group"/>
                </FormGroup>
                <Button onClick={this.handleAddNewUser}>Add</Button>
            </Form>
        );
    }
}


export default FormAddingUser